<template>
  <div class="signature-creator">
    <h1>Classy Llama Email Signature Creator</h1>
    <p><b>Instructions:</b> Fill out with your information, click COPY NOW, and paste into your email client signature settings. (Visit on your phone to use for mobile signatures too.)</p>
    <br>
    <form class="form" @submit.prevent="copy">
      <div class="form-group">
        <label for="name">Name</label><input @blur="store" @focus="reset" name="name" type="text" v-model="name" required spellcheck="off" autocapitalize="words" autocorrect="off">
      </div>
      <div class="form-group">
        <label for="title">Title</label><input @blur="store" @focus="reset" name="title" type="text" v-model="title" required autocapitalize="words">
      </div>
      <div class="form-group">
        <label for="certifications">Certifications</label><input @blur="store" @focus="reset" name="certifications" type="text" v-model="certifications">
      </div>
      <div class="form-group">
        <label for="phone">Phone Number</label><input @blur="store" @focus="reset" name="phone" type="text" v-model="phone">
      </div>
      <div class="form-group">
        <label for="ext">Phone Ext</label><input @blur="store" @focus="reset" name="ext" type="text" v-model="ext">
      </div>
      <div class="form-group">
        <label for="mobile">Mobile</label><input @blur="store" @focus="reset" name="mobile" type="text" v-model="mobile">
      </div>
      <div class="form-group">
        <label for="email">Email</label><input @blur="store" @focus="reset" name="email" type="email" v-model="email">
      </div>
      <div class="form-group">
        <label for="linkedin">Linkedin</label><input placeholder="username" @blur="store" @focus="reset" name="linkedin" type="text" v-model="linkedin" spellcheck="off" autocapitalize="off">
      </div>
      <div class="form-group">
        <label for="twitter">Twitter</label><input placeholder="username" @blur="store" @focus="reset" name="twitter" type="text" v-model="twitter" spellcheck="off" autocapitalize="off">
      </div>
      <div class="form-group">
        <label for="github">Github</label><input placeholder="username" @blur="store" @focus="reset" name="github" type="text" v-model="github" spellcheck="off" autocapitalize="off">
      </div>
      <div class="form-group">
        <label for="isNucleus" class="checkbox" @click="isNucleus = !isNucleus"><input @change="store" @focus="reset" name="isNucleus" type="checkbox" v-model="isNucleus"> Nucleusify</label>
      </div>
      <div class="form-group">
        <button type="submit" @click="copy">Copy Now</button> <span class="success" v-if="successful">Copied to your clipboard!</span> <span class="error" v-if="errorMsg">{{ errorMsg }}</span>
      </div>
    </form>
    <p class="small-label">Signature Preview:</p>
    <div class="signature-wrapper" :contenteditable="selecting" @click="noClick" :class="noSelectClass">
      <br align="center" class="float-center"><br align="center" class="float-center"><br align="center" class="float-center"><span v-html="gmailStyles"/><table class="row cl-signature" :style="tableStyle"><tbody><tr style="padding:0;text-align:left;vertical-align:top"><th v-if="!shouldChangeMobile" class="small-4 large-4 columns first" valign="middle" style="Margin:0 auto;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:16px;font-weight:400;line-height:1.3;margin:0 auto;padding:0;padding-bottom:0;padding-left:0;padding-right:8px;text-align:left;width:105px"><table style="border-collapse:collapse;border-spacing:0;padding:0;text-align:left;vertical-align:top;width:100%"><tr style="padding:0;text-align:left;vertical-align:top"><th style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:16px;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left"><a target="_blank" rel="noopener" href="https://www.classyllama.com/" style="Margin:0;background:#fff;color:#777;display:block;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left;text-decoration:none"><img :src="logoSrc" width="100" height="100" align="right" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:block;outline:0;text-decoration:none;width:100px;height:100px;"></a></th></tr></table></th><th class="small-8 large-8 columns last" :style="thStyle" valign="middle"><table style="border-collapse:collapse;border-spacing:0;padding:0;text-align:left;vertical-align:top;width:100%"><tr style="padding:0;text-align:left;vertical-align:top"><th style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:16px;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left"><p class="lead" style="Margin:0;Margin-bottom:8px;background:#fff;color:#555;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:16px;font-weight:800;line-height:24px;margin:0;margin-bottom:0;padding:0;padding-left:2px;text-align:left">{{ name }}</p><p style="Margin:0;Margin-bottom:8px;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:13px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;padding-left:2px;text-align:left;white-space:nowrap;" v-html="formattedTitle"></p><p class="small" style="Margin:0;Margin-bottom:8px;white-space:nowrap;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left">&nbsp;</p><p v-if="certifications" class="small" style="Margin:0;Margin-bottom:8px;white-space:nowrap;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left"><img src="https://files.classyllama.com/bc9abf9a/certificate2x.png" alt="cert: " width="16" height="16" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:inline;max-width:100%;outline:0;text-decoration:none;vertical-align:text-bottom;width:auto;padding-right:3px;">{{ certifications }}</p><p v-if="phone" class="small" style="Margin:0;Margin-bottom:8px;white-space:nowrap;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left"><a target="_blank" rel="noopener" :href="tel" style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left;text-decoration:none"><img src="https://files.classyllama.com/bc9abf9a/phone2x.png" alt="p: " width="16" height="16" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:inline;max-width:100%;outline:0;text-decoration:none;vertical-align:text-bottom;width:auto;padding-right:3px;">{{ phone }}</a> <span v-if="ext">ext {{ ext }}</span></p><p v-if="mobile" class="small" style="Margin:0;Margin-bottom:8px;white-space:nowrap;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left"><a target="_blank" rel="noopener" :href="telMobile" style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left;text-decoration:none"><img src="https://files.classyllama.com/bc9abf9a/mobile2x.png" alt="p: " width="16" height="16" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:inline;max-width:100%;outline:0;text-decoration:none;vertical-align:text-bottom;width:auto;padding-right:3px;">{{ mobile }} </a></p><p v-if="email" class="small" style="Margin:0;Margin-bottom:8px;white-space:nowrap;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left"><a target="_blank" rel="noopener" :href="mailto" style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left;text-decoration:none"><img src="https://files.classyllama.com/bc9abf9a/mail2x.png" alt="e: " width="16" height="16" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:inline;max-width:100%;outline:0;text-decoration:none;vertical-align:text-bottom;width:auto;padding-right:3px;">{{ email }}</a></p><p v-if="linkedin" class="small" style="Margin:0;Margin-bottom:8px;white-space:nowrap;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left"><a target="_blank" rel="noopener" :href="linkedinHref" style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left;text-decoration:none"><img src="https://files.classyllama.com/bc9abf9a/linkedin2x.png" alt="in: " width="16" height="16" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:inline;max-width:100%;outline:0;text-decoration:none;vertical-align:text-bottom;width:auto;padding-right:3px;">{{ linkedin }}</a></p><p v-if="twitter" class="small" style="Margin:0;Margin-bottom:8px;white-space:nowrap;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left"><a target="_blank" rel="noopener" :href="twitterHref" style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left;text-decoration:none"><img src="https://files.classyllama.com/bc9abf9a/twitter2x.png" alt="tw: " width="16" height="16" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:inline;max-width:100%;outline:0;text-decoration:none;vertical-align:text-bottom;width:auto;padding-right:3px;">{{ twitter }}</a></p><p v-if="github" class="small" style="Margin:0;Margin-bottom:8px;white-space:nowrap;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:12px;font-weight:400;line-height:16px;margin:0;margin-bottom:0;padding:0;text-align:left"><a target="_blank" rel="noopener" :href="githubHref" style="Margin:0;background:#fff;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-weight:400;line-height:1.3;margin:0;padding:0;text-align:left;text-decoration:none"><img src="https://files.classyllama.com/bc9abf9a/github2x.png" alt="github: " width="16" height="16" style="-ms-interpolation-mode:bicubic;border:none;clear:both;display:inline;max-width:100%;outline:0;text-decoration:none;vertical-align:text-bottom;width:auto;padding-right:3px;">{{ github }}</a></p></th></tr></table></th></tr></tbody></table>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'SignatureCreator',
    props: ['isMobile'],
    data() {
      var localData = localStorage.getItem('cl-signature') || '{}'
      localData = JSON.parse(localData);
      return Object.assign({
        isNucleus: false,
        name: 'Your Name',
        title: 'Your Title',
        certifications: 'Magento Certified (DP, FED, SS), Zend CE',
        phone: '(417) 866-8887',
        ext: '',
        mobile: '',
        email: 'firstname.lastname@classyllama.com',
        twitter: '',
        linkedin: '',
        github: '',
        selecting: false,
        successful: null,
        errorMsg: null,
        noSelectClass: this.isMobile ? '' : 'noselect',
        changeForMobile: false,
        gmailStyles: `<style>.gmail_signature a,.cl-signature a,.gmail_signature a[href],.cl-signature a[href],.gmail_signature a:hover,.cl-signature a:hover{text-decoration: none !important;}</style>`
      }, localData);
    },
    computed: {
      formattedTitle() {
        return this.title + (this.title ? ', ' : '') + (this.isNucleus ? 'Nucleus&nbsp;Support' : 'Classy&nbsp;Llama')
      },
      shouldChangeMobile() {
        return this.changeForMobile && this.isMobile
      },
      tableStyle() {
        // var signatureWidth = this.shouldChangeMobile ? 271 : 500;
        // return `Margin:0;border-collapse:collapse;border-spacing:0;float:none;margin:0;max-width:${signatureWidth}px;padding:0;position:relative;text-align:left;vertical-align:top;width:${signatureWidth}px`;
        return `Margin:0;border-collapse:collapse;border-spacing:0;float:none;margin:0;padding:0;position:relative;text-align:left;vertical-align:top;width:auto;min-width:300px;`;
      },
      thStyle() {
        var borderWidth = this.shouldChangeMobile ? 0 : 1;
        var paddingX = this.shouldChangeMobile ? 0 : 16;
        return `Margin:0 auto;background:#fff;border-left:${borderWidth}px solid #e9e9e9;color:#777;font-family:'Open Sans',Helvetica,Arial,sans-serif;font-size:16px;font-weight:400;line-height:1.3;margin:0 auto;padding:0;padding-bottom:4px;padding-left:${paddingX}px;padding-right:0;padding-top:0;text-align:left;`;
      },
      tel() {
        return `tel:${this.phone.replace(/[^\d]+/g, '')}`;
      },
      telMobile() {
        return `tel:${this.mobile.replace(/[^\d]+/g, '')}`;
      },
      mailto() {
        return `mailto:${this.email}`;
      },
      twitterHref() {
        return `https://twitter.com/${this.twitter}`;
      },
      linkedinHref() {
        return `https://www.linkedin.com/in/${this.linkedin}`;
      },
      githubHref() {
        return `https://github.com/${this.github}`;
      },
      logoSrc() {
        return this.isNucleus ? 'https://files.classyllama.com/bc9abf9a/email-signature-nuc200px_x_200px.png' : 'https://files.classyllama.com/bc9abf9a/email-signature-llama200px_x_200px.png';
      }
    },
    methods: {
      store() {
        localStorage.setItem('cl-signature', JSON.stringify(this.$data));
        // clean data
        if (this.linkedin) {
          var linkedin = this.linkedin.split('/').filter(function(item) { return item != ''});
          this.linkedin = linkedin[linkedin.length - 1];
        }
        if (this.twitter) {
          var twitter = this.twitter.split('/').filter(function(item) { return item != ''});
          this.twitter = twitter[twitter.length - 1].replace('@', '');
        }
        if (this.github) {
          var github = this.github.split('/').filter(function(item) { return item != ''});
          this.github = github[github.length - 1];
        }
      },
      copy() {
        this.store();
        this.selecting = true;
        this.noSelectClass = '';
        var self = this;
        setTimeout(function(){
          var el = document.querySelector('.signature-wrapper')
          var range = document.createRange();
          range.selectNodeContents(el);
          var sel = window.getSelection();
          sel.removeAllRanges();
          sel.addRange(range);

          try {
            self.successful = document.execCommand('copy');
            self.errorMsg = null;
          } catch (err) {
            self.successful = false;
            self.errorMsg = err;
          }
          sel.removeAllRanges();
          self.selecting = false;
          self.noSelectClass = this.isMobile ? '' : 'noselect';
        }, 0);
      },
      reset() {
        this.successful = null;
        this.errorMsg = null;
      },
      noClick() {
        this.successful = null;
        this.errorMsg = `Click COPY NOW when you're ready to copy.`
      },
    }
  }
</script>

<style scoped>
  .signature-creator {
    overflow: hidden;
  }
  .form {
    margin-bottom: 40px;
  }
  .form-group {
    margin-bottom: 16px;
  }
  input {
    font-size: 16px;
    height: 16px;
    padding: 7px 7px;
    border: 1px solid #ddd;
    border-radius: 2px;
  }
  label {
    display: inline-block;
    width: 150px;
    margin-right: 8px;
    font-weight: 600;
    padding: 4px 0;
    text-align: right;
  }
  label.checkbox {
    width: auto;
    margin-left: 158px;
  }
  button {
    border: 0;
    background: #52D4DB;
    color: #fff;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    border-radius: 5px;
    padding: 0 16px;
    margin-left: 158px;
    margin-right: 16px;
  }
  button:hover,
  button:focus {
    cursor: pointer;
    background: #74E6EC;
    outline: none;
  }
  p {
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 16px;
  }
  .small-label {
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 8px;
    font-weight: 600;
  }
  .signature-wrapper {
    padding: 15px;
    background: #fff;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  .noselect {
    -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */
       -khtml-user-select: none; /* Konqueror HTML */
         -moz-user-select: none; /* Firefox */
          -ms-user-select: none; /* Internet Explorer/Edge */
              user-select: none; /* Non-prefixed version, currently
                                    supported by Chrome and Opera */
  }
  .success {
    color: #01D296;
  }
  .error {
    color: #E23B2C;
  }

  @media screen and (max-width: 500px) {
    label {
      width: auto;
      display: block;
      margin: 0;
      text-align: left;
    }
    label.checkbox {
      margin-left: 0;
    }
    input {
      display: inline-block;
      width: calc(100% - 16px);
    }
    label.checkbox input {
      width: auto;
    }
    button {
      margin-left: 0;
      width: 100%;
      margin-right: 0;
    }
    .success,
    .error {
      display: block;
      margin: 8px 0;
    }
    .signature-wrapper {
      overflow-x: scroll;
    }
  }
</style>


